import React, { useEffect, useState } from 'react';
import { loadUsers } from '../../api/users';
import { User } from '../../types';
import './UserList.scss'

export const UserList: React.FC = () => {
  const [users, setUsers] = useState<User[]>([]);

  const getUsers = async () => {
    const users = await loadUsers()
    setUsers(users)
  };

  useEffect(() => {
    getUsers();
  }, [])

  return (
    <div className="users">
      <form
        method="GET"
        action=""
        className="addUserForm"
      >
        <label htmlFor="name">
          Name:
          <input type="text" />
        </label>

        <label htmlFor="username">
          Login:
          <input type="text" />
        </label>

        <label htmlFor="mail">
          Mail:
          <input type="text" />
        </label>

        <label htmlFor="phone">
          Phone number:
          <input type="text" />
        </label>

        <label htmlFor="site">
          Link to you website:
          <input type="text" />
        </label>
        <button type="submit">
          Add user
        </button>
      </form>
      <ul className="users__list">
        {users.map(user => (
          <li key={user.id}>
            <h2>{user.name}</h2>
            <p>{user.username}</p>
            <a
              target="_blank"
              href={`https://${user.website}`}
              rel="noreferrer"
            >
                {user.website}
            </a>
            <button type="button">
              Learn more
            </button>
            <button type="button">
              Delete
            </button>
          </li>
        ))}
      </ul>
    </div>
  )
}
